<template>
  <div >
    <Admin v-if="page == 0" @addName="stuName = $event" @next="page++"/>
    <Question :qData="qData" :page="page" />
    <Report v-if="page == 6" :stuName="stuName" :houseReport="houseReport" />
  </div>
</template>

<script>
import Question from "./components/Question.vue";
import Admin from "./components/Admin.vue";
import Report from "./components/Report.vue";
import qData from "./qData";
import rData from "./rData";

export default {
  name: "App",
  components: {
    Question,
    Admin,
    Report,
  },
  data() {
    return {
      page: 0,
      score: 0,
      qData,
      rData,
      stuName: '',
      houseReport: '',
    };
  },
  mounted() {
    this.emitter.on('addScore', a => {
      this.score += a;
      this.page++;
    }),
    this.emitter.on('house', a => {
      this.score += a;
      if(this.score <= 4) {
        this.houseReport = this.rData[0];
      } else if (4 < this.score && this.score <= 8) {
        this.houseReport = this.rData[2];
      } else if (8 < this.score && this.score <= 12) {
        this.houseReport = this.rData[3];
      } else if (12 < this.score && this.score <= 16) {
        this.houseReport = this.rData[4];
      } else if (16 < this.score && this.score <= 20) {
        this.houseReport = this.rData[1];
      }
      this.page ++;
      console.log(this.score);
      console.log(this.houseReport);
    }),
    this.emitter.on('start', () => {
      this.page = 0;
      this.score = 0;
      this.stuName = '';
      this.houseReport = '';
      console.log(this.page);
      console.log(this.score);
    })
  },
}
</script>

