<template>
  <div class="a-inner">
    <div class="a-item" v-for="(a, i) in qData.answer" :key="i">
      <label>
        <input
          type="radio"
          :value="qData.answer[i].score"
          v-model="currentScore"
        />
        <div
          class="answer"
          :style="`background-image: url(${qData.answer[i].image})`"
        >
          <p style="text-align: left">{{ qData.answer[i].content }}</p>
        </div>
      </label>
    </div>
    <button class="btn" v-if="page !== 5" @click="addScore">제출</button>
    <button class="btn" v-if="page == 5" @click="selectHouse">제출</button>
  </div>
</template>

<script>
export default {
  name: "Answer",
  data() {
    return {
      currentScore: 0,
    };
  },
  props: {
    qData: Object,
    page: Number,
  },
  methods: {
    addScore() {
      this.emitter.emit("addScore", this.currentScore);
    },
    selectHouse() {
      this.emitter.emit("house", this.currentScore);
    }
  },
};
</script>

<style>
.a-item input[type=radio] {
  display:none
}
.a-item input[type=radio]:checked + .answer {
  border: solid 8px darkslateblue;
}
.answer {
  width: 23vw;
  height: 23vw;
  background-position: center;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 10px;
}
.answer:hover {
  cursor: pointer;
}
.a-inner {
  width: 50vw;
  display: flex;
  flex-wrap: wrap;
  padding: 2vw;
}
.btn {
  width: 100px;
  height: 40px;
  padding: 5px;
  background-color: #eee;
  margin: 5px auto;
  font-weight: 800;
}
@media screen and (max-width: 768px) {
  .a-inner {
    width: 100vw;
    padding: 4vw;
  }
  .answer {
    width: 46vw;
    height: 46vw;
  }
}
</style>